.keyboard {
  overflow-y: auto;
  width: 100%;
}

  .keyboard-notes {
    display: -ms-flexbox;
    display: flex;
    -ms-flex: none;
        flex: none;
  }

  .keyboard-col {
    display: -ms-flexbox;
    display: flex;
    -ms-flex: none;
        flex: none;
    -ms-flex-direction: column;
        flex-direction: column;
    margin: 0 1px;
  }

  /* holds 2 accidentals */
  .keyboard-acc2 {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    height: 78px;
    -ms-flex-pack: center;
        justify-content: center;
  }

  /* holds 3 accidentals */
  .keyboard-acc3 {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    height: 104px;
    -ms-flex-pack: center;
        justify-content: center;
  }

.keyboard::-webkit-scrollbar {
  background-color: inherit;
  width: 10px;
}

.keyboard::-webkit-scrollbar-thumb {
  background-color: black;
  background-color: rgba(0, 0, 0, 0.3);
  border-radius: 5px;
  width: 5px;
}



