.editable-text {
  background-color: inherit;
  color: inherit;
  font-size: inherit;
  height: 100%;
  padding: 2px;
  width: 100%;
}

.editable-text-div {
  border-bottom: 1px solid transparent;
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  white-space: nowrap;
}

.editable-text-input {
  border: none;
  border-bottom: 1px solid transparent;
}

.editable-text-input:focus {
  border-bottom: 1px solid #AAA;
  outline: none;
}




